.invitame{
    font-size: 1em;
}

.anahi_ruben{
    font-family: 'Tangerine';

    margin-top: 50px;
}

.presentacion{
  margin-top: -400px;
  
  
}
.button{
    margin: auto;
    background-color: rgba(188, 79, 175, 0.876);
    border-radius: 10px;
    font-size: 30px;
    padding: 20px;
    text-align: center;
    width: 320px;
    margin-bottom: 100px;
    margin-top: 20px;
    transition: all ease .7s;
}
.button:hover{
    background-color: rgba(237, 98, 221, 0.623);
    cursor: pointer;
    transform: scale(1.3);

}
.button a{
   text-decoration: none;
   color: white;
}


.whatsapp_contact {
    position: fixed;
    right: 20px;
    bottom:20px;
    width: 50px;
    z-index: 10;
    
}

.whatsapp_contact img {
    width: 50px;
}




.nuestra_boda{
text-align: center;
font-size: 3em;
z-index: 10;

}

.anahi_ruben{
    text-align: center;
    font-size: 8em;
     z-index: 10;
     
}
.info{
    margin-top: 230px;
}
.btn_map:hover{
    background-color: rgba(0, 0, 0, 0.308);
}

.info_flex{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 50px;
}
.info_flex img{
    border-radius: 10px;
    width: 100%;
    height: 300px;
}

.donde{
    background-color: rgba(250, 215, 244, 0.527);
    text-align: center;
    margin-top: 50px;
    padding: 20px;
    border-bottom: 1px solid #ddd;
}

.ceremonia, .recepcion, .itinerario{
    padding: 20px;
    border: 1px solid #ddd;
    margin: 20px;
    background-color: white;
}

.btn_map{
    background-color: rgba(0,0,0,0.2);
    text-align: center;
    border-radius: 10px;
    width: 200px;
    padding: 10px 20px 10px 20px;
    margin: auto;
    margin-top: 20px;
}
.btn_map a{
 text-decoration: none;
 color: black;
}
.btn_map_hide{
    background-color:white;
    text-align: center;
    border-radius: 10px;
    width: 200px;
    padding: 10px 20px 10px 20px;
    margin: auto;
    margin-top: 20px;
}

@media(max-width:768px) {
 .invitame{
    display: none;
 }   
 .presentacion h2{
    font-size: 3em;
 }
 .presentacion{
    padding-top: 150px;
 }
.info{
    background-color: rgba(250, 215, 244, 0.527);
   
    
}
.donde{
    background-color: rgba(250, 215, 244, 0.527);
    margin-top: -100px;
    padding: 20px;
    border-bottom: 1px solid #ddd;
   
}
.info_flex{
    margin-top: 0px;
}

}