.header_flex{
 background: url('../src/img/11.jpg')no-repeat center center/cover;
 width: 100%;
 height: 800px;
}
.header_flex img{
    width: 100%;
    object-fit: cover;
}

.nav{
    width: 100%;
    height: 60px;
    top: 0;
    position: fixed;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: white;
    /* background-color: rgba(0,0,0,0.1); */
    padding: 20px;
}
.nav ul{
display: flex;
width: 100%;
list-style: none;
}

.nav ul a{
    margin: 20px;
    text-decoration: none;
    color: black;
    font-weight: bold;
    
 
}
.invitame{
    font-size: 1.3em;
    color: rgb(180, 59, 164);
}


@media(max-width:768px) {
    .header_flex{
       height: 500px;
       font-size: .8em;
    }  
    .nosotros{
        display: none;
    }
    .nav{
        padding: 10px;
    }
}